import React from "react";
import styles from "./ProjectItem.module.css"




let ProjectItem = () => {
    return (
            <div className={styles.project__item}>
                <div className={styles.project}>
                    <img src="data:image/jpeg;base64," alt="project"/>
                    <a href="#">Смотреть</a>
                </div>

                <div className={styles.project__about}>
                    <h3>Подробное описание проекта</h3>
                    <p>Описание</p>
                </div>
            </div>

    )
};

export default ProjectItem;